html {
  font-size: 100%; }

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Hiragino Kaku Gothic ProN", Meiryo, sans-serif;
  font-size: 1rem;
  letter-spacing: .05em; }

.u-container {
  padding: 40px; }

._blind {
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
  width: 1px;
  height: 1px;
  font-size: 1px;
  line-height: 100px;
  white-space: nowrap; }

i {
  display: inline-block;
  font-style: normal; }

.u-header {
  display: flex;
  align-items: center;
  height: 80px;
  padding: 0 40px;
  background-color: white; }
  .u-header > p {
    max-width: 500px;
    color: color(black a(0.7)); }
  .u-header._fixed {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 19999; }
  .u-header._theme-dark {
    background-color: #37474f; }
    .u-header._theme-dark > p {
      color: color(white a(0.7)); }

.u-header-logo {
  margin: 0 20px 0 0;
  color: black; }
  .u-header-logo > sup {
    vertical-align: top;
    display: inline-block;
    padding-top: 16px;
    font-size: .75rem;
    font-weight: normal; }
  ._theme-dark .u-header-logo {
    color: white; }
